<template>
  <div class="record">
    <div class="record-input">
      <el-tabs v-model="chooseType" type="card">
        <el-tab-pane label="按玩家查询" name="first">
          <el-radio-group v-model="player" size="small" @input="query">
            <el-radio-button
              v-for="(item, index) in playerList"
              :label="item"
              :key="index"
            ></el-radio-button>
          </el-radio-group>
        </el-tab-pane>
        <el-tab-pane label="按昵称查询" name="second">
          <el-checkbox-group v-model="checkList" size="small">
            <el-checkbox
              :label="item.id"
              v-for="item of useridList"
              :key="item.id"
              >{{ item.user }}</el-checkbox
            >
          </el-checkbox-group>
        </el-tab-pane>
        <el-tab-pane label="按账号查询" name="third">
          <el-input
            type="textarea"
            style="width: 100%; max-width: 603px"
            autosize
            v-model="accouts"
            size="small"
            placeholder="请输入游戏登录账号，多个账号用英文逗号,分隔"
          ></el-input>
        </el-tab-pane>
      </el-tabs>
      <div class="query-date">
        <el-date-picker
          v-model="date"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          :picker-options="pickerOptions"
          size="small"
          @change="dateChange"
          style="width: 260px; margin-right: 12px"
          :clearable="false"
          append-to-body
        >
        </el-date-picker>
        <el-button
          type="warning"
          icon="el-icon-search"
          size="small"
          @click="query"
        >
          查询
        </el-button>
        <el-button
          plain
          type="primary"
          size="small"
          icon="el-icon-refresh"
          @click="reset"
        >
          清空
        </el-button>
      </div>
      <div class="record-title">
        排位对局：<span class="color-orange">{{ tableData.length }}</span> ，
        胜场：<span class="color-red">{{ win }}</span
        >， 败场：<span class="color-green">{{ lost }}</span
        >， 胜率：<span class="color-blue">{{ rate }}%</span>
      </div>
    </div>

    <div class="record-output" v-loading="loading">
      <el-table
        :data="tableData"
        :border="true"
        :header-cell-style="{ 'text-align': 'center' }"
        :cell-style="{ 'text-align': 'center' }"
        style="width: 603px"
        v-if="tableData.length > 0"
      >
        <el-table-column prop="endtime" label="日期" width="157">
          <template slot-scope="scope">
            {{
              parseTime(
                scope.row.endtime - 8 * 3600,
                "{m}月{d}日 {h}:{i}",
                "sgs"
              )
            }}
          </template>
        </el-table-column>
        <el-table-column prop="subSeat" label="位置" width="63">
        </el-table-column>
        <el-table-column prop="subGeneral" label="副将" width="105">
        </el-table-column>
        <el-table-column prop="mainGeneral" label="主将" width="105">
        </el-table-column>
        <el-table-column prop="mainSeat" label="位置" width="63">
        </el-table-column>
        <el-table-column prop="result" label="胜负" width="88">
          <template slot-scope="scope">
            <el-tag :type="scope.row.result ? 'success' : 'danger'">
              {{ scope.row.result ? "负" : "胜" }}
            </el-tag>
          </template>
        </el-table-column>
        <template slot="empty">
          <img src="@/assets/img/empty.png" />
          <div>{{ emptyText }}</div>
        </template>
      </el-table>
      <div class="empty" v-else>
        <img src="@/assets/img/empty.png" />
        <div class="mt-15">{{ emptyText }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import md5 from "js-md5";
import wjList from "./wj.json";
import useridMap from "./userid.json";

export default {
  data() {
    return {
      loading: false,
      chooseType: "first",
      playerList: [
        "小胡",
        "cbb",
        "zhu老师",
        "时滞",
        "霜迟",
        "经纪人",
        "逍遥",
        "浪心",
        "白银",
        "埃城",
      ],
      playerMap: {
        小胡: ["曹丕", "甄姬"],
        cbb: ["孙策", "大乔"],
        zhu老师: ["丿丶沐辞意", "丿丶予詩情"],
        时滞: ["时滞丷", "丷九月", "丶清嘉", "丶晨光熹微"],
        霜迟: ["谈杯", "鸢尾丶修竹"],
        经纪人: ["剑刺de苍穹", "声"],
        孤妄: ["孤妄", "睡莲"],
        逍遥: ["尘封到逍遥", "追流星的风"],
        白银: ["桃黑酒红黑", "☆小鑫鑫★"],
        浪心: ["诗与岁月同歌", "前尘镜"],
        埃城: ["埃城", "王异"],
      },
      pickerOptions: {
        shortcuts: [
          {
            text: "最近七天",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 6);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "本月",
            onClick(picker) {
              const end = new Date();
              const start = new Date(end.getFullYear(), end.getMonth(), 1);
              start.setTime(start.getTime());
              picker.$emit("pick", [start, end]);
            },
          },
        ],
        firstDayOfWeek: 1,
        disabledDate(date) {
          let now = Date.now();
          let range = (now - date.getTime()) / (1000 * 3600 * 24);
          return range < 0;
        },
      },
      date: null,
      dateList: [],
      player: "小胡",
      checkList: [],
      accouts: "",
      recordList: [],
      recordObj: {},
      tableData: [],
      seatArr: [4, 1, 2, 3],
      win: 0,
      lost: 0,
      rate: 0,
    };
  },
  computed: {
    useridList() {
      let arr = [];
      for (let key in useridMap) {
        if (key != "comment")
          arr.push({
            user: key,
            id: useridMap[key],
          });
      }
      return arr;
    },
    emptyText() {
      if (this.dateList.length > 1) {
        return "该玩家摆烂了，这些天都没有打排位。";
      } else if (this.dateList == this.parseTime(Date.now(), "{y}{m}{d}")) {
        return "该玩家摆烂了，今天没有打排位。";
      } else {
        return "该玩家摆烂了，这天没有打排位。";
      }
    },
  },
  mounted() {
    this.date = [new Date(), new Date()];
    this.dateList.push(this.parseTime(Date.now(), "{y}{m}{d}"));
    this.query();
  },
  watch: {
    recordList: {
      handler(val) {
        this.handleBtn();
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    parseTime(time, pattern, type) {
      if (arguments.length === 0 || !time) {
        return null;
      }
      const format = pattern || "{y}-{m}-{d} {h}:{i}:{s}";
      let date;
      if (typeof time === "object") {
        date = time;
      } else {
        if (typeof time === "string" && /^[0-9]+$/.test(time)) {
          time = parseInt(time);
        } else if (typeof time === "string") {
          time = time
            .replace(new RegExp(/-/gm), "/")
            .replace("T", " ")
            .replace(new RegExp(/\.[\d]{3}/gm), "");
        }
        if (typeof time === "number" && time.toString().length === 10) {
          time = time * 1000;
        }
        date = new Date(time);
      }
      const formatObj = {
        y: date.getFullYear(),
        m: date.getMonth() + 1,
        d: date.getDate(),
        h: date.getHours(),
        i: date.getMinutes(),
        s: date.getSeconds(),
        a: date.getDay(),
      };
      const time_str = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
        let value = formatObj[key];
        if (key === "a") {
          return ["日", "一", "二", "三", "四", "五", "六"][value];
        }
        if (type == "sgs") {
          if (result.length > 0 && value < 10 && key != "m" && key != "d") {
            value = "0" + value;
          }
          return value || 0;
        }
        if (result.length > 0 && value < 10) {
          value = "0" + value;
        }
        return value || 0;
      });
      return time_str;
    },
    query() {
      this.recordList = [];
      this.tableData = [];
      let userIdArr;
      if (this.chooseType == "first") {
        userIdArr = this.playerMap[this.player].map(
          (item) => useridMap[item] || useridMap["comment"][item]
        );
      } else if (this.chooseType == "second") {
        userIdArr = this.checkList;
      } else if (this.chooseType == "third") {
        userIdArr = this.accouts.split(",").map((item) => md5(item));
      }
      userIdArr.forEach((userid) => {
        if (!userid) return;
        this.dateList.forEach((date) => {
          this.getRecord(userid, date);
        });
      });
    },
    getRecord(userid, date) {
      this.loading = true;
      let obj = {
        areaid: "1100",
        t: parseInt(new Date().getTime() / 1000),
        userid,
        date,
      };
      axios({
        url: "https://ngx.sanguosha.com/sgs_games_list",
        method: "POST",
        data: this.formatAxiosData(obj),
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
      }).then((res) => {
        if (res.data && res.data.length > 0) {
          this.recordList.push(...res.data);
        }
        this.loading = false;
      });
    },
    formatAxiosData(obj) {
      const arr = [];
      for (let key in obj) {
        arr.push(`${encodeURIComponent(key)}=${encodeURIComponent(obj[key])}`);
      }
      return arr.join("&");
    },
    handleBtn() {
      this.recordObj = {};
      this.recordList.forEach((item) => {
        let str1 = item
          .replaceAll("uuid=;", "")
          .replaceAll("=", '":')
          .replaceAll(";", ',"')
          .replaceAll("{", "[")
          .replaceAll("}", "]");
        let str2 = '{"' + str1.slice(0, -2) + "}";
        let game = JSON.parse(str2);
        if (game.labelmodel != 132) {
          return;
        }
        if (!this.recordObj[game.gameid]) {
          this.recordObj[game.gameid] = game;
        }
        this.setSeat(game);
      });
      this.formatTableData();
    },
    setSeat(game) {
      if (game.seatId == 1 || game.seatId == 3) {
        this.recordObj[game.gameid].mainSeat = "1/3";
        this.recordObj[game.gameid].mainGeneral = wjList[game.generals[0]];
      } else {
        this.recordObj[game.gameid].subSeat = "2/4";
        this.recordObj[game.gameid].subGeneral = wjList[game.generals[0]];
      }
    },
    formatTableData() {
      this.tableData = [];
      for (let key in this.recordObj) {
        this.tableData.push(this.recordObj[key]);
      }
      this.tableData.sort(this.compare("gameid"));
      this.win = 0;
      this.lost = 0;
      this.rate = 0;
      if (this.tableData.length < 1) return;
      this.tableData.forEach((item) => {
        if (item.result) {
          this.lost++;
        } else {
          this.win++;
        }
      });
      this.rate = Number((this.win / this.tableData.length) * 100).toFixed(1);
    },
    compare(property, desc) {
      return function (a, b) {
        var value1 = a[property];
        var value2 = b[property];
        return desc ? value2 - value1 : value1 - value2;
      };
    },
    reset() {
      this.tableData = [];
      this.accouts = "";
      this.win = 0;
      this.lost = 0;
      this.rate = 0;
    },
    dateChange(e) {
      if (!e) {
        this.dateList = [];
        this.dateList.push(this.parseTime(Date.now(), "{y}{m}{d}"));
        return;
      }
      let time = e[0].getTime();
      let range = Math.floor(
        (e[1].getTime() - e[0].getTime()) / (1000 * 3600 * 24) + 1
      );
      this.dateList = [];
      for (let i = 0; i < range; i++) {
        let str = this.parseTime(time + i * 1000 * 60 * 60 * 24, "{y}{m}{d}");
        this.dateList.push(str);
      }
      this.query();
    },
  },
};
</script>

<style lang="scss" scope>
* {
  box-sizing: border-box;
}

.record {
  height: 100vh;
  padding: 20px 20px;
  display: flex;
  flex-direction: column;
}

.record-input {
  flex: 0;
  position: relative;
  width: 100%;
  /* min-width: 620px; */
}

.record-output {
  flex: 1;
  position: relative;
  width: 100%;
  max-width: 605px;
  height: 0;
}

.query-date {
  display: flex;
  align-items: center;
  margin-top: 12px;
}

.record-title {
  margin: 10px 0;
  font-size: 16px;
  /* height: 28px; */
  line-height: 28px;
}

.record-title span {
  font-size: 16px;
}

.record-title .right {
  display: flex;
}

.time-detail {
  position: absolute;
  top: 10px;
  right: 2px;
}

.query-btn {
  position: absolute;
  bottom: 10px;
  left: 440px;
}

.total {
  padding-bottom: 10px;
}

.el-table {
  height: 100%;
}

.el-table__body-wrapper {
  height: calc(100% - 44px);
  overflow: auto;
}

.el-textarea__inner {
  overflow: auto;
}

.el-radio-button--mini .el-radio-button__inner {
  padding: 7px 10px;
}

.el-textarea__inner {
  padding: 4px 15px;
}

.el-date-editor .el-range__close-icon {
  width: 10px;
}

.el-tabs__item {
  height: 34px;
  line-height: 34px;
}

.el-tabs__content {
  margin-top: 2px;
}

.el-checkbox {
  margin-right: 10px;
}

.el-checkbox__label {
  padding-left: 4px;
}

.el-checkbox-group {
  padding: 5px 0 4px;
}

.chat-room__list .msg-bubble-decorationPrefix,
.chat-room__list .msg-bubble .msg-bubble-icon,
.chat-room__list .msg-bubble-decorationSuffix,
#editorWrapper,
.room-gg-chat,
.chat-room__list .msg-normal-decorationPrefix,
.box-noble-level-1 {
  display: none !important;
}

.chat-room__list .msg-bubble {
  background-image: none !important;
}

.chat-room__list .msg {
  color: #333 !important;
}

.chat-room__bd .chat-room__scroll:hover .clearBtn,
.chat-room__bd .chat-room__scroll:hover .lockBtn {
  display: block;
  top: 5px !important;
}

.chat-room__list .name,
.chat-room__list .msg-bubble .name {
  color: #b6cce0 !important;
}

.el-table .el-table__cell {
  padding: 10px 0 !important;
}

.el-tag {
  height: 28px;
  line-height: 26px;
}

.el-date-editor .el-range-separator {
  width: 20px;
}

.el-tag.el-tag--success {
  background-color: #ebf9ef;
  border-color: #d8f3e8;
  color: #3cc265;
}

.el-button--warning {
  color: #ffffff;
  background-color: #ffba00;
  border-color: #ffba00;
}

.el-button--warning:hover,
.el-button--warning:focus {
  background: #ffc833;
  border-color: #ffc833;
  color: #ffffff;
}

.el-table .el-table__header-wrapper th,
.el-table .el-table__fixed-header-wrapper th {
  word-break: break-word;
  background-color: #f8f8f9;
  color: #515a6e;
  height: 40px;
  font-size: 13px;
}

.el-radio-button--small .el-radio-button__inner {
  padding: 7px 14px;
  font-size: 14px;
}

.el-table__empty-text {
  color: #c29e9e;
}

.el-radio-group {
  display: flex;
  overflow-x: auto;
}

@media screen and (max-width: 415px) {
  .el-tabs__nav {
    width: 100%;
    display: flex;
    justify-content: space-around;
  }
}

.empty {
  width: 100%;
  height: 100%;
  border: 1px solid #ebeef5;
  padding-bottom: 20%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #c29e9e;
}
</style>